<template>
  <div class="path-bar">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item v-for="(item, index) in pathArray" :key="index" :to="item.link">{{ item.name }}</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="logout">
      <img src="@/assets/logo.png" alt="广汽丰田logo">
      <span>积分服务中心</span>
      <el-link
        :underline="false"
        icon="el-icon-upload2"
        @click.prevent="onConfirm"
        v-loading.fullscreen.lock="isLoading"
      >登出</el-link>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  name: 'PathBar',
  data () {
    return {
      pathArray: [],
      isLoading: false
    }
  },
  watch: {
    // 根据路由路径来设置面包屑内容
    $route: {
      handler: function (route) {
        this.pathArray = route.matched.map(match => ({
          link: match.path,
          name: match.meta.name
        }))
      },
      immediate: true
    }
  },
  methods: {
    ...mapMutations(['setAccountInfo']),

    // 登出确认
    onConfirm () {
      this.$confirm('是否确认登出？', '提示', {
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.onLogout()
      })
    },

    // 登出处理
    onLogout () {
      this.isLoading = true
      this.$http.serviceLogout()
        .then(() => {
          this.clearAccountInfo()
          this.$message.success('已登出')
          this.$router.replace('/login')
        })
        .catch(error => {
          if (!error.code && error.name) Promise.reject(error)
          else this.$alert(error.message, '登出失败', { type: 'error' })
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    // 清空用户信息缓存
    clearAccountInfo () {
      sessionStorage.removeItem('accountInfo')
      this.setAccountInfo(null)
    }
  }
}
</script>

<style lang="scss" scoped>
.path-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 0;

  ::v-deep .el-breadcrumb__item:not(:last-child) .el-breadcrumb__inner.is-link {
    font-weight: 400;
    color: #606266;
    &:hover {
      color: #409eff;
    }
  }

  .logout {
    display: flex;
    align-items: center;
    img {
      margin-right: 10px;
      height: 20px;
    }
    span {
      margin-right: 10px;
      font-size: 14px;
      line-height: 20px;
    }
    ::v-deep [class^=el-icon-] {
      transform: rotate(90deg);
    }
  }
}
</style>
